.wordlists {
    height: 100%;
    padding: 5px 10px;
    box-sizing: border-box;
    max-width: 100vw;
}
.wordlists > div {
    height: 100%;
    display: flex;
    overflow-x: auto;
}
.wordlists .new {
    width: 380px;  
}
.wordlists .new a{
    padding: 5px 15px;
    width: 340px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 50%;
    border: 1px solid gray;
    border-radius: 5px;
    margin: 5px;
    font-size: 18px;
    font-weight: 900;
}
.wordlists .new .title {
    font-size: 36px;
    width: 300px;
    padding-bottom: 10px;
}
.wordlists .new button {
    width: 100%;
}
.wordlists .new>div {
    font-size: 42px;
    font-weight: 900;
    font-family: sans-serif;
    text-align: center;
    margin-top: 125px;
}
.wordlists ul {
    padding: 0px;
    min-width: 400px;
    width: 400px;
    border: 1px solid gray;
    border-radius: 5px;
    margin: 5px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.wordlists .title {
    display: flex;
    justify-content: space-between;
    min-height: 80px;
    max-height: 80px;
    width: 380px;
    border-bottom: 3px solid black;
    margin-bottom: 5px;
    font-size: 23px;
    font-weight: 900;
    color: green !important;
    margin: 10px;
}
.wordlists ul .title > div:first-child {
    overflow-x: hidden;
    overflow-y: auto;
    width: 360px;
    margin: 0 5px 5px 0;
}
.wordlists .title a {
    color: green !important;
}
.wordlists ul .title > div:nth-child(2) {
    height: 50%;
}
.wordlists ul .title button {
    height: 100%;
}
.wordlists ul a{
    height: 100%;
}
.wordlists ul>div:nth-child(2) {
    overflow-x:hidden;
    overflow-y:auto;
}
    
.wordlists ul li {
    padding: 5px 10px;
}
.wordlists ul li div{
    display: flex;
    align-items: center;
}
.pencil {
   font-family: serif;
   font-size: 23px;
}
.wordlists ul>div:last-child {
    display: flex;
    justify-content: center;
}
.wordlists .delete {
    color: transparent;
    font-weight: 900;
    font-family: sans-serif;
    border: 2px solid black;
    border-radius: 5px;
    margin: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 60%;
    height: 30px;
    cursor: pointer;
    overflow: hidden;
}
.wordlists .delete div {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    transition: 0.7s;
}
.wordlists .delete div:hover {
    color: black;
}
.wordlists .confirm {
    color: white;
    background-color: red;
    width: 60%;
    border-right: 1px solid black;
}
.wordlists .cancel {
    color: white;
    background-color: #2D8CFF;
    width: 40%;
    border-left: 1px solid black;
}
.wordlists .delete .question {
    color: red;
    width: 100%;
}
.zero {
    width: 0;
}
.zero span {
    visibility: hidden;
}
.new-wordlist {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.new-wordlist > div {
    border: 1px solid gray;
    border-radius: 10px;
    min-width: 50%;
    padding: 15px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: white;
}

.new-wordlist>div>div:nth-of-type(2) {
    background-color: lightgray;
    border: 5px solid black;
    font-weight: 900;
    font-size: 36px;
    border-radius: 10px;
    width: 200px;
    padding: 15px;
    display: flex;
    justify-content: center;
}
.save-button {
    color: green;  
    cursor: pointer;
}
.save-button-disabled {
    color: gray;
}
.disclaimer {
    font-style: oblique;
    font-size: 12px;
    font-family: sans-serif;
    margin: 5px;
}
.new-wordlist .title {
    display: flex;
    width: 100%;
    justify-content: center;
    align-items: center;
}
.new-wordlist .title button  {
    max-height: 80px;
    height: 80px;
    max-width: 80px;
    width: 80px;
    margin-right: 25px;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0;
}
.new-wordlist .title button div {
    max-height: 80px;
    max-width: 80px;
    font-size: 42px;
    margin-left: 10px;
}
.new-wordlist .title label {
    display: flex;
    align-items: center;
    font-family: sans-serif;
}
.new-wordlist div > input {
    font-size: 24px;
    width: 50%;
    padding: 5px 10px;
    margin: 0 10px 10px 0px;
}
.new-wordlist form div {
    display: flex;
    width: 100%;
    align-items: center;
    font-family: sans-serif;
}
.new-wordlist form div input {
    font-size: 18px;
    width: 100%;
    margin: 10px;
}
.new-wordlist div div span {
    font-weight: 900;
    margin-right: 5px;
}
.new-wordlist ul {
    border: 1px solid black;
    min-height: 20px;
    min-width: 50%;
    max-width: 50%;
    padding: 10px;
    transition: 0.4s;
}
@media screen and (max-width: 1600px) {
    .new-wordlist ul {
        min-width: 80%;
        max-width: 80%;
        transition: 0.4s;
    }
}

button div {
    margin: 3px;
}
.new-wordlist button .pencil {
    font-size: 18px;
}
.new-wordlist > div > form {
    display: flex;
    /* flex-direction: column; */
    justify-content: center;
    align-items: center;
}
.new-wordlist form div input:last-of-type {
    cursor: pointer;
}
.new-wordlist a div {
    font-size: 36px;
    font-weight: 900;
    color: blue;
    border-radius: 5px;
    width: 500px;
    padding: 5px 15px;
}

ul li {
    margin: 0px auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 90%;
    font-family: sans-serif;
}

ul li button {
    margin: 0 5px 3px 0;
    cursor: pointer;
    height: 33px;
    width: 70px;
    margin-right: 10px;
    flex: 0 0 0;
}
.phrase li > div {
    width: 100%;
    display: flex;
    align-items: center;
}
.phrase li form {
    width: 80%;
    margin-right: 20px;
}
.phrase li input {
    min-width: 100%;
    margin: 0 0 5px 0;
}
.phrase > div:last-child {
    border-bottom: 5px solid lightgray;
    border-top: 3px solid transparent;
    border-radius: 25px;
    width: 80%;
    margin: 1px auto 5px auto;
    transition: 0.12;
}
.phrase > div:last-child:hover {
    border-bottom: 5px solid #2D8CFF;
    border-top: 3px solid #2D8CFF;
    transition: 0.1s;
    cursor: pointer;
}