.wordlists {
    height: 100%;
    padding: 5px 10px;
    box-sizing: border-box;
    max-width: 100vw;
}
.wordlists > div {
    height: 100%;
    display: flex;
    overflow-x: auto;
}
.wordlists .new {
    width: 380px;  
}
.wordlists .new a{
    padding: 5px 15px;
    width: 340px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 50%;
    border: 1px solid gray;
    border-radius: 5px;
    margin: 5px;
    font-size: 18px;
    font-weight: 900;
}
.wordlists .new .title {
    font-size: 36px;
    width: 300px;
    padding-bottom: 10px;
}
.wordlists .new button {
    width: 100%;
}
.wordlists .new>div {
    font-size: 42px;
    font-weight: 900;
    font-family: sans-serif;
    text-align: center;
    margin-top: 125px;
}
.wordlists ul {
    padding: 0px;
    min-width: 400px;
    width: 400px;
    border: 1px solid gray;
    border-radius: 5px;
    margin: 5px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.wordlists .title {
    display: flex;
    justify-content: space-between;
    min-height: 80px;
    max-height: 80px;
    width: 380px;
    border-bottom: 3px solid black;
    margin-bottom: 5px;
    font-size: 23px;
    font-weight: 900;
    color: green !important;
    margin: 10px;
}
.wordlists ul .title > div:first-child {
    overflow-x: hidden;
    overflow-y: auto;
    width: 360px;
    margin: 0 5px 5px 0;
}
.wordlists .title a {
    color: green !important;
}
.wordlists ul .title > div:nth-child(2) {
    height: 50%;
}
.wordlists ul .title button {
    height: 100%;
}
.wordlists ul a{
    height: 100%;
}
.wordlists ul>div:nth-child(2) {
    overflow-x:hidden;
    overflow-y:auto;
}
    
.wordlists ul li {
    padding: 5px 10px;
}
.wordlists ul li div{
    display: flex;
    align-items: center;
}
.pencil {
   font-family: serif;
   font-size: 23px;
}
.wordlists ul>div:last-child {
    display: flex;
    justify-content: center;
}
.wordlists .delete {
    color: transparent;
    font-weight: 900;
    font-family: sans-serif;
    border: 2px solid black;
    border-radius: 5px;
    margin: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 60%;
    height: 30px;
    cursor: pointer;
    overflow: hidden;
}
.wordlists .delete div {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    transition: 0.7s;
}
.wordlists .delete div:hover {
    color: black;
}
.wordlists .confirm {
    color: white;
    background-color: red;
    width: 60%;
    border-right: 1px solid black;
}
.wordlists .cancel {
    color: white;
    background-color: #2D8CFF;
    width: 40%;
    border-left: 1px solid black;
}
.wordlists .delete .question {
    color: red;
    width: 100%;
}
.zero {
    width: 0;
}
.zero span {
    visibility: hidden;
}
.new-wordlist {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.new-wordlist > div {
    border: 1px solid gray;
    border-radius: 10px;
    min-width: 50%;
    padding: 15px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: white;
}

.new-wordlist>div>div:nth-of-type(2) {
    background-color: lightgray;
    border: 5px solid black;
    font-weight: 900;
    font-size: 36px;
    border-radius: 10px;
    width: 200px;
    padding: 15px;
    display: flex;
    justify-content: center;
}
.save-button {
    color: green;  
    cursor: pointer;
}
.save-button-disabled {
    color: gray;
}
.disclaimer {
    font-style: oblique;
    font-size: 12px;
    font-family: sans-serif;
    margin: 5px;
}
.new-wordlist .title {
    display: flex;
    width: 100%;
    justify-content: center;
    align-items: center;
}
.new-wordlist .title button  {
    max-height: 80px;
    height: 80px;
    max-width: 80px;
    width: 80px;
    margin-right: 25px;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0;
}
.new-wordlist .title button div {
    max-height: 80px;
    max-width: 80px;
    font-size: 42px;
    margin-left: 10px;
}
.new-wordlist .title label {
    display: flex;
    align-items: center;
    font-family: sans-serif;
}
.new-wordlist div > input {
    font-size: 24px;
    width: 50%;
    padding: 5px 10px;
    margin: 0 10px 10px 0px;
}
.new-wordlist form div {
    display: flex;
    width: 100%;
    align-items: center;
    font-family: sans-serif;
}
.new-wordlist form div input {
    font-size: 18px;
    width: 100%;
    margin: 10px;
}
.new-wordlist div div span {
    font-weight: 900;
    margin-right: 5px;
}
.new-wordlist ul {
    border: 1px solid black;
    min-height: 20px;
    min-width: 50%;
    max-width: 50%;
    padding: 10px;
    transition: 0.4s;
}
@media screen and (max-width: 1600px) {
    .new-wordlist ul {
        min-width: 80%;
        max-width: 80%;
        transition: 0.4s;
    }
}

button div {
    margin: 3px;
}
.new-wordlist button .pencil {
    font-size: 18px;
}
.new-wordlist > div > form {
    display: flex;
    /* flex-direction: column; */
    justify-content: center;
    align-items: center;
}
.new-wordlist form div input:last-of-type {
    cursor: pointer;
}
.new-wordlist a div {
    font-size: 36px;
    font-weight: 900;
    color: blue;
    border-radius: 5px;
    width: 500px;
    padding: 5px 15px;
}

ul li {
    margin: 0px auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 90%;
    font-family: sans-serif;
}

ul li button {
    margin: 0 5px 3px 0;
    cursor: pointer;
    height: 33px;
    width: 70px;
    margin-right: 10px;
    flex: 0 0;
}
.phrase li > div {
    width: 100%;
    display: flex;
    align-items: center;
}
.phrase li form {
    width: 80%;
    margin-right: 20px;
}
.phrase li input {
    min-width: 100%;
    margin: 0 0 5px 0;
}
.phrase > div:last-child {
    border-bottom: 5px solid lightgray;
    border-top: 3px solid transparent;
    border-radius: 25px;
    width: 80%;
    margin: 1px auto 5px auto;
    transition: 0.12;
}
.phrase > div:last-child:hover {
    border-bottom: 5px solid #2D8CFF;
    border-top: 3px solid #2D8CFF;
    transition: 0.1s;
    cursor: pointer;
}
.game {
    display: flex;
    justify-content: space-around;
    width: 100%;
}
.game > div:first-child {
    width: 150px;
    font-size: 50px;
    margin: 100px 0px 0px 80px;
    display: flex;
    flex-direction: column;
    align-items: center;
    transition: 0.4s;
}
.game .play {
    border: 10px solid lightgray;
    border-radius: 40px;
    padding: 20px;    
    color: lightgray;
    margin-top: 50px;
    -webkit-user-select: none;
            user-select: none;
    transition: 0.4s;
}
.game .win {
    border: 10px solid green;
    border-radius: 40px;
    padding: 20px; 
    margin-top: 50px;
    -webkit-user-select: none;
            user-select: none;
    transition: 0.4s;
}
.game > div:nth-child(2) {
    display: flex;
    flex-direction: column;
    align-items: center;
}
.game .thead {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 10px;
    height: 100px;
    max-height: 100px;
    height: 100px;
    min-height: 100px;
    width: 100%;
    transition: 0.4s;
}
.game .thead > div{
    max-height: 80px;
    min-height: 80px;
    max-width: 800px;
    min-width: 80px;
    font-size: 33px;
    text-align: center;
    overflow-y: auto;
    overflow-x: hidden;
    transition: 0.4s;
    font-weight: 900;
    padding: 10px 5px 5px 5px;;
    font-family: sans-serif;
}
.game .thead button {
    max-height: 80px;
    height: 80px;
    max-width: 80px;
    width: 80px;
    transition: 0.4s;
}
.game button .pencil {
    font-size: 42px;
    transition: 0.4s;
}
.game > div:last-child {
    width: 200px;
    margin: 50px 50px 0 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    transition: 0.4s;
}

.game > div:last-child button {
    padding: 20px; 
    margin: 40px;
    font-size: 40px;
    font-weight: 900;
    cursor: pointer;
    transition: 0.4s;
}
.game label {
    font-size: 23px;
    font-weight: 900;
    font-family: sans-serif;
    width: 120%;
    margin-left: 25px;
    transition: 0.4s;
}
.game label input {
    margin: 5px;
}
.game table {
    border: 5px solid black;
    padding: 5px;
    border-radius: 10px;
    font-family: sans-serif;
    border-spacing: 5px;
    transition: 0.4s;
}
.game table td {
    background-color: white;
    border: 1px solid black;
    font-size: 23px;
}
.game table td div {
    margin: 5px;
    max-width: 136px;
    width: 136px;
    max-height: 136px;
    height: 136px;
    overflow-y: auto;
    overflow-x: hidden;
    -webkit-user-select:text;
            user-select:text;
    transition: 0.4s;
}
.free-spot {
    color: white;
    font-weight: 900;
    -webkit-user-select: none;
            user-select: none;
    cursor: not-allowed !important;  
    background-color: #2D8CFF !important;
    transition: 0.2s;
}
.free-spot div {
    font-size: 36px;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: 0.4s;
}
.unclicked {
    -webkit-user-select: none;
            user-select: none;
    transition: 0.4s;
    cursor: pointer;
}
.clicked {
    background-color: #2D8CFF !important;
    color: white;
    -webkit-user-select: none;
            user-select: none;
    transition: 0.2s;
    font-weight: 900;
    cursor: pointer;
}
.winner {
    font-weight: 900;
    background-color: #FF372D !important;
    color: white;
    transition: 0.4s;
    cursor: pointer;

}
.blackout td {
    font-weight: 900;
    background-color: #123 !important;
    color: white;
    transition: 0.8s;    
}
.disabled {
    color: lightgray;
}

@media screen and (max-width: 1600px) {
    .game > div:first-child {
        margin-top: 150px;   
        transition: 0.4s;
        margin-left: 50px;
    }
    .game .play {
        font-size: 36px;
        padding: 10px;
        transition: 0.4s;
    }
    .game .win {
        font-size: 36px;
        padding: 10px;
        transition: 0.4s;
    }
    .game .thead {
        max-height: 50px;
        min-height: 50px;
        transition: 0.4s;
    }
    .game .thead > div{
        font-size: 23px;
        max-height: 50px;
        min-height: 50px;
        transition: 0.4s;
    }
    .game .thead button {
        height: 50px;
        width: 50px;
        transition: 0.4s;
    }
    .game button .pencil {
        font-size: 27px;
        margin-left: -7px;
        transition: 0.4s;
    }
    .game table {
        border: 3px solid black;
        padding: 1px;
        border-radius: 5px;
        font-family: sans-serif;
        border-spacing: 3px;
        transition: 0.4s;
    }
    .game table td div {
        font-size: 14px;
        margin: 3px;
        max-width: 64px;
        width: 64px;
        max-height: 64px;
        height: 64px;
        transition: 0.4s;
    }
    .free-spot div {
        font-size: 23px;
        transition: 0.4s;
    }
    .game > div:last-child button {
        padding: 10px;
        margin: 20px;
        font-size: 20px;
        transition: 0.4s;
    }
    .game label {
        font-size: 14px;
        transition: 0.4s;
    }
}
a, a:visited, a:hover, a:link, a:active {
    text-decoration: none; 
    color: black;
}
*:disabled {
    cursor: not-allowed !important;
}
body {
    background-image: url('/bingo-hall3.jpg');
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    background-attachment: fixed; 
    height: 100vh;
    width: 100%;
    margin: 0;
    padding: 0px 100px;
    box-sizing: border-box;
}
#root {
    width: 100%;
    height: 100%;
}
.app {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
}
.main{
    background-color: rgba(255, 255, 255, 0.94);
    width: 100%;
    min-height: 100%;
    border-radius: 25px;
    box-sizing: border-box;
    transition: 0.4s;
}
.buttonbar {
    position: fixed;
    top: 5px;
    left: 10px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    height: 100%;
}
.buttonbar div {
    display: flex;
    flex-direction: column;   
}
.buttonbar button {
    font-size: 50px;
    height: 80px;
    width: 80px;
    margin-top: 25px; 
    transition: 0.4s;
}
.buttonbar img {
    width: 50px;
    height: 50px;
    background-color: white;
    border-radius: 50px;
    padding: 10px;
    margin-bottom: 25px;
    transition: 0.4s;
}


.hidden {
    visibility: hidden;
}

button {
    cursor: pointer;
}
.splash {
    margin: 123px;
}
.login-form  {
    width: 300px;
}
.login-form div {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    
}
.login-form div input {
    width: unset;
}
.splash input {
    margin: 5px;
    width: 280px;
}
.submit-btn {
    font-weight: 900;
    cursor: pointer;
}
.demo-user-btn {
    font-weight: 900;
    color: green;
    cursor: pointer;
}
.splash ul {
    color: red;
}
.login-form p {
    margin-left: 5px;
}
.signup-link {
    color: green !important;
    font-weight: 900;
}
@media screen and (max-width: 1600px) {
    .main {
        margin-left: -100px;
        margin-right: -100px;
        flex: 1 1 100%;
        border-radius: 0;
        transition: 0.4s;
    }
    .buttonbar button {
        height: 50px;
        width: 50px;
        font-size: 30px;
        transition: 0.4s;
    }
    .buttonbar button span {
        margin-left: -7px;
        transition: 0.4s;
    }
    .buttonbar img {
        width: 40px;
        height: 40px;
        padding: 5px;
        transition: 0.4s;
    }
}
